.question{
    position: absolute;
    z-index: 1000;
    font-family: 'NEXTMonoPan-Thin' !important;
    color: white;
    font-size: 20px;
    height: 0vh;
    // background-color: red;
    margin-top: 45vh;
    left: 50%;
    transform: translate(-50%, -50%);

}

.mobile-loading{
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: #272727;
  opacity: 0;
  z-index: -1;
  animation-name: mobile-loading;
  animation-duration: 1.3s;
}
@keyframes mobile-loading {
  1%   {
    z-index: 99999999;
    opacity: 1;
  }
  70%  {opacity: 1;}
  100% {
    opacity: 0;
  }
}


.bottom-bar{
    position: absolute;
    z-index: 10000000;
    bottom: 0;
}
  
@keyframes slidein {
    from {
      margin-left: 100%;
      width: 300%; 
    }
  
    to {
      margin-left: 0%;
      width: 100%;
    }
  }

  @media(max-width: 600px),screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {


    .container{
      overflow: hidden;
    }
    .logo{
      // display: none;
