.card-wrapper {
  position: relative;
  height: 100%;
  transition: all 1s;
  width: 100%;
  background: white;
}

.card-cover {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, .8);
}

.hidden {
  visibility: hidden;
}

.off-line {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-between;

  .line {
    opacity: 0.04;
    width: 1px;
    background-color: #d8d8d8;
  }
}

.gradiant {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 400px;
  width: 100%;
  background: rgb(2, 0, 36);
  background: linear-gradient(0deg, rgba(2, 0, 36, 1) 0%, rgba(0, 0, 0, 0.5970763305322129) 0%, rgba(0, 212, 255, 0) 100%);
}

.layer {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: url("../../../assets/bg-texture.png");
  background-repeat: repeat;
}

.second-card-background {
  overflow-x: hidden;

}


.card-cover:hover {
  display: none;
}

.item-column {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: all 1s;
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
  transition: all .8s ease;
  opacity: 0.04;

  &:hover {
    opacity: 0.85;
  }
}

.first-card-background {
  overflow-x: hidden;

  .item-column {
    background-size: cover;
    background-position-x: center;
  }
}

.full-width {
  width: 100vw;
  transition: all 1s;

  .item-column {
    background-size: cover;
    background-position-x: 25vw;
    filter: brightness(1);

    &:hover {
      background-size: cover;
      background-position-x: 25vw;
      filter: brightness(1);
    }
  }

  .center-woman {
    background-position-x: 0
  }

  .necklace-move {
    background-position-y: -21vh;

  }
}

.no-width {
  transition: all 1s;
  width: 0;
}

.full-light {
  filter: brightness(1);
}

.type-mobile {
  visibility: hidden;

}

@media(max-width: 600px) {

  .no-width {
    transition: all 1s;
    height: 0 !important;
    width: 0 !important;
  }

  .item-column {
    //opacity: 0.85;
  }

  .full-width {
    height: 90vh !important;
    width: 100vw !important;
    background-color: black;
    transition: all 1s;

    .item-column {
      background-size: cover;
      background-position: center;
      filter: brightness(1);

      &:hover {
        background-size: cover;
        background-position: 50%;
        filter: brightness(1);
      }
    }
  }

  .card-wrapper {
    position: relative;
    background-size: cover;
    background-position: 50%;
    width: 100%;

    .item-column {
      .hide-line-mobile {
        visibility: hidden;
      }
    }

    .type-mobile {
      visibility: visible;
      position: absolute;
      background-color: black;
      width: 100%;
      height: 4vh;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      z-index: 100000;
      bottom: 0;
    }
  }

  .necklace-move {
    background-position-y: -30vh !important;
    background-color: black;
  }

  .other-move {
    background-position-y: -3vh !important;
    background-color: black;
  }
}
