.bottom-container {
  width: 100%;
  height: 6.4%;
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  color: rgba(0, 0, 0, .6);
  font-family: 'NEXTMonoPan-Thin';
  font-size: 14px;
  z-index: 100000;
  padding-left: 2vw;
}

.not-active-bottom-item {
  color: rgba(0, 0, 0, .4);

}

.menu-item {
  padding-left: 60px;
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  &:first-child{
    padding-left: 40px;
  }
}

.end-item {
  position: absolute;
  right: 2.1vw;
}

.dropdown-content {
  position: absolute;
  bottom: 6.4vh;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  text-align: center;
}

.dropdown-content-category {
  justify-content: center !important;
  align-items: flex-start !important;
}

.menu-item:hover .dropdown-content {
  visibility: visible;

}

.category-width {
  width: 246px;
  left: 10px;
  overflow: hidden;
  padding: 0 40px;
}

.sub-menu-item {
  background-color: #000000;
  opacity: 0.7;
  height: 48px;
  line-height: normal;
  color: #ffffff;
  padding: 0 40px;
  margin-top: 1px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  white-space: nowrap;
  font-size: 14px;
  font-weight: 100;
  letter-spacing: 0.5px;
}

.sub-menu-item:hover {
  opacity: 0.8;
}

.visibility-hidden {
  visibility: hidden;
}

.price-item {
  position: relative;
  background-color: #dfdfdf;
  color: #9a9a9a;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 20px;
  padding-right: 20px;
  height: 4.4vh;
  line-height: 4.4vh;
  width: 100%;
  font-size: 14px;
  letter-spacing: 0.5px;
}

.price-text {
  color: #4d4d4d;
}

.price-dropdown {
  position: absolute;
  bottom: 6.4vh;;
  text-align: left;
  z-index: 1;
  background-color: #dfdfdf;
  width: 23.6vw;
  visibility: hidden;
}

.menu-item-mobile {
  visibility: hidden;
}


.mobile-menu {
  display: none;
}

.menu-item:hover .price-dropdown {
  visibility: visible;
}


@media(max-width: 600px), screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {

  .bottom-container {
    height: 8.1vh;
    display: flex;
    flex-direction: row;
    align-items: center;

    justify-content: space-between;

    .menu-item {
      visibility: hidden;
    }

    .price-dropdown {
      visibility: hidden;

    }

    .button-position-mobile {
      position: absolute;
      right: 5vw;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 14px;
    }

    .select-disable {
      color: #e2e2e2;
    }

    .menu-item-mobile {
      visibility: visible;
      display: flex;
      flex-direction: row;
      font-size: 12px;
      letter-spacing: 1px;
      color: #000000;

      .arrow {
        width: 20px;
        height: 20px;
        padding-right: 4.7vw;
        padding-left: 4.7vw;
      }

      .menu-morse-mobile {
        color: white;
        background-color: black;
        width: 32px;
        height: 24px;
        font-size: 12.8px;
        display: flex;
        align-items: center;
        justify-content: center;
        -webkit-text-stroke: 0.8px #ffffff;
        box-shadow: 1px 3px 11px 0 rgba(0, 0, 0, 0.2);
        border-radius: 2px;
        box-shadow: 1px 3px 11px 0 rgba(0, 0, 0, 0.2);
        object-fit: contain;
        letter-spacing: 1.6px;
      }
    }

    .morse-menu-item {
      position: absolute;
      bottom: 2.5vh;
      left: 8vw;
    }

    .mobile-menu {
      display: block;
      position: absolute;
      z-index: 10000;
      width: 100%;
      bottom: 100%;
      right: 0%;

      .menu-item-sub {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding-left: 4.2vw;
        font-size: 12px;
        height: 8.6vh;
        width: 100%;
        border-top: solid #aaaaaa 0.1px;
        opacity: 0.85;
        background-color: #ffffff;

        .dropdown-icon {
          position: absolute;
          right: 14vw;
          width: 24px;
          height: 24px;
        }
      }

      .menu-item-sub-black {
        opacity: 0.85;
        background-color: #000000;
        letter-spacing: 0.43px;
        color: #ffffff;
      }
    }
  }
}

