.input-wrapper-all {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 30%;
}

.morse-text-input {
  font-size: 7vw;
}


.placeholder {
  font-size: 7vw;
  background-color: transparent;
  border: 0;
  outline: none;
  align-self: center;
  font-family: 'TimesNow-Light';
  text-transform: uppercase;
  font-weight: 100;
  text-align: center;
  letter-spacing: 1.6vw;
  font-variant-numeric: tabular-nums;
  text-transform: full-width;
  color: rgba($color: #FFFFFF, $alpha: .30);
}

.morse-text-input {
  position: absolute;
  background-color: transparent;
  border: 0;
  color: transparent;
  outline: none;
  align-self: center;
  font-family: 'TimesNow-Light';
  text-transform: uppercase;
  font-weight: 200;
  text-align: center;
  letter-spacing: 3vw;
  font-variant-numeric: tabular-nums;
  text-transform: full-width;
  z-index: 1000;
  caret-color: white;
  width: 600px;
}


.morse-text-input:first-line, .morse-text-input-div:first-line, .span-char-input:first-line {
  background-color: transparent;
}


.morse-text-input-div {

  color: white;
  outline: none;
  font-family: 'TimesNow-Light';
  text-transform: uppercase;
  font-weight: 200;
  text-align: center;
  font-variant-numeric: tabular-nums;
  text-transform: full-width;
  display: flex;
  align-items: center;
  justify-content: center;
}


.span-char-input {
  position: relative;
  width: 6.5vw;
  font-size: 7vw;
  color: white;
}

.long-text-width {
  width: 4.5vw;
  font-size: 5vw;

}

.too-long-text-width {
  width: 2.7vw;
  font-size: 2.7vw;

}

.text-span-input {
  color: white;
  text-transform: uppercase;
  display: flex;
  max-width: 100vw;
  flex-direction: row;
  text-align: center;
  font-variant-numeric: tabular-nums;
  text-transform: full-width;
  font-family: 'TimesNow-Light';
  align-items: center;
  justify-content: center;
}

.thank-you {
  height: 82px;
  font-size: 100px;
  font-weight: 200;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;

  .long-text-width {
    width: unset;
    font-size: unset;
    padding-left: 20px;

    @media (max-width: 600px) {
      padding-left: 16px;
    }
  }

  @media (max-width: 600px) {
    font-size: 50px;
    line-height: 1.4;
    text-align: center;
  }
}

.code-morse {
  color: white;
  margin-top: 15vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: .6vw;
}

.error-message {
  color: red;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: 1px;
}

.suggestions {
  color: white;
  display: flex;
  align-items: left;
  justify-content: center;
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: 1px;
}

.icon {
  width: 20px;
  height: 8px;
  padding-left: 2px;
}


.char {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.invalid-validations {
  display: none;
}

.error-position {
  width: 43.19vw;
  font-size: 14px;
  margin-top: 25vh;
  position: absolute;
}

.red {
  color: #ff3232 !important;
}

.dash-width-input {
  width: 20px;
}

// .dash-width-input{
//     width: 1.5vw;
//     height: 0.555vw;
// }
// .dot-width-input{
//     width: .7vw;
//     height: 1.3vh;
// }

.dot-width-input {
  width: 8px;
  height: 8px;
}

.input-morse-width-shift {
  text-indent: -20vw;
}


@media(max-width: 600px), screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {

  .placeholder {
    font-size: 13vw;
    letter-spacing: 1vw;
  }

  .text-span-input {
    overflow: hidden;
    // max-width: 100vw;
  }

  .error-position {
    width: 90vw;
    text-align: center;
    margin-top: 13vh;

    .error-message {
      // color:white;
      display: flex;
      align-items: left;
      justify-content: center;
      font-size: 12px !important;
      letter-spacing: 1px;
      line-height: 1.5;
      letter-spacing: 1px;
    }
  }

  .morse-text-input {
    font-size: 12vw;
    overflow-x: hidden;
    max-width: 100vw;
  }


  .span-char-input {
    position: relative;
    width: 10vw;
    font-size: 12vw;
    color: white;
  }

  .thank-you {
    .span-char-input {
      position: relative;
      width: 10vw;
      font-size: 50px;
      color: white;
      padding-left: 16px;
    }
  }

  .dash-width-input {
    width: 12px;
    padding-left: 1px;
    height: 7px;

  }
  .dot-width-input {
    width: 7px;
    height: 7px;
  }
}
