.material-page{
    // background-color: white;
    background-color: transparent;
    // opacity: .9;
    width: 100%;
    height:100%;
    position: absolute;
}

.bb{background-color: transparent;}

.grid-container{
    width: 100%;
    height: 100%;
    // display: flex;
    // align-items: center;
    // justify-content: center;
}

.mini-item {
    display: flex;
    align-items: center;
    justify-content: center;
}

.mini-item-material{
    
    filter: brightness(.3);
}
.mini-item-material>*{
    filter: brightness(1);
    
}

.material-image{
    z-index: 100000000 !important;
    width: 25%;
    transition: all .5s;

}


.material-image:hover{
    width: 30%;
}

@media(max-width: 600px),screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {

    .material-image{
        width: 70%;
    }

    .material-image:hover{
        width: 80%;
    }


    .type-mobile-material{
        visibility: visible;
        position: absolute;
        background-color: black;
        width: 100%;
        height: 4vh;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        z-index: 100000;
        bottom: 0;
        // filter: brightness(120);
    }
}