.grid-container{
    width: 100%;
    height: 100%;
    display: flex;
}


.right-image-container{
    background: #4c4c4c;
}

.mouse-movement{
    position: absolute;
    // transition: all 0.01s ease;
    color: #ffffff;
    text-shadow: 0 2px 16px #000000;
    align-self: center;
    text-align: center;
    letter-spacing: 1px;
    height: 17px;
    z-index: 1000;
    font-family: 'NEXTMonoPan-Thin';
    font-size: 14px;
    font-weight: 100;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
}

.mouse-movement:hover{
    pointer-events: none;

}


.mini-item{
    width: 50%;
    position: relative;
}
@keyframes mymove {
    from {height: 0px;}
    to {height: 100vh;}
}
.first-card-background{
    background: #333134;
}


@keyframes mymovesecond {
    from {height: 0px;}
    to {height: 90vh;}
}


@keyframes mymove-chrome {
    from {height: 0px;}
    to {height: 100vh;}
}

@keyframes mymovesecond-chrome {
    from {height: 0px;}
    to {height: 90vh;}
}

@media(max-width: 600px),screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {
    @keyframes mymove {
        from {height: 0px;}
        to {height: 90vh;}
    }




    @keyframes mymove-chrome {
        from {height: 0px;}
        to {height: 78vh;}
    }

    @keyframes mymovesecond {
        from {height: 0px;}
        to {height: 30vh;}
    }


    @keyframes mymovesecond-chrome {
        from {height: 0px;}
        to {height: 26.3vh;}
    }



    .second-grid-selected {
        flex-direction: column;

      }



    .second-grid {
        flex-direction: column;
        align-items: center;
        .mini-item{
            // width: 100%;
            height:30.65vh;
            animation-name: mobile-cards-spread !important;
            animation-duration: 0.5s;

        }

        @keyframes mobile-cards-spread {
            from {width: 60%;}
            to {width: 100%;}
        }

      }


      .mini-item{
        width: 100%;
        height:91vh;
    }

    .mouse-movement{
        display: none;
        width: 0vh;
    }

  }


