.container-morse {
  width: 100vw;
  height: 100vh;
  position: absolute;
  justify-content: center;
  overflow-x: hidden;
}

.container-morse-mini-text {
  @media (min-width: 600px) {
    .span-char-input {
      width: 4.5vw;
      font-size: 4.5vw;
    }
  }
}


.first-morse {
  filter: brightness(0.35);
  // background-color: red;
  // margin-right: 70%;
}

.model-image {
  background-image: url(../../../assets/images/model-5.png);
  background-color: white;
  background-repeat: no-repeat;
  background-size: contain;
  height: 100%;;
  width: 60%;
  z-index: 20000;
}

.logo {
  background-image: url(../../../assets/images/logo/logo.svg);
  background-size: cover;
  position: absolute;
  z-index: 99999999;
  width: 111px;
  height: 72px;
  margin-top: 44px;
  object-fit: contain;
}


.back-to-shop-card {
  position: absolute;
  top: 40vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  @media (max-width: 600px) {
    top: 34vh;
  }

  .description {
    position: relative;
    top: 74px;
    color: #ffffff;
    text-align: center;
    padding: 10px 0;
    height: 27px;
    font-size: 14px;
    font-weight: 100;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.93;
    letter-spacing: normal;
    text-align: center;

    @media (max-width: 600px) {
      height: 44px;
      font-size: 12px;
      line-height: 1.83;
      padding: 40px 10px;
    }
  }

  .back-to-shop {
    position: fixed;
    bottom: 20vh;
    height: 17px;
    background: black;
    color: #ffffff;
    opacity: 0.8;
    padding: 15px 25px;
    font-size: 14px;
    font-weight: 100;
    letter-spacing: 0.7px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    white-space: nowrap;
    text-align: center;

    img {
      padding-left: 8px;
    }

    @media (max-width: 600px) {
      display: none;
    }
  }
}


