.paint-morse {
  position: absolute;
  background-attachment: fixed;
  // height: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
  // max-height: 250px;

  @media (max-width: 600px) {
    // margin-bottom: 2px;
    // max-height: 110px;
    overflow: hidden;

  }


  .morse-border {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

  .tasti {
    color: grey;
    z-index: 10000;
    font-size: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 7px;
    flex-direction: column;

    @media (max-width: 600px) {
      margin-bottom: 2px;
    }

  }

  @keyframes morse-new-letter {
    0% {
      fill: #989898;
    }
    50% {
      fill: white;
    }
    100% {
      fill: white;
    }
  }

  .white-morse-fill {
    fill: white;
  }

  .dot-icon {
    // width:8px ;
    // height:8px ;
  }

  .morse-char {
    margin-bottom: 3px;
    animation-name: morse-new-letter;
    fill: #989898;
    animation-duration: 1s;
    @media (max-width: 600px) {
      margin-bottom: 2px;
    }
  }

  .dash-svg {
    width: 8px;
    height: 24px;
  }
}


.icon-background {
  width: 1.4vw;
  height: 1.4vh;
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}

.morse-btns {
  // background-color: red;
  position: absolute;
  transform: translate(-50%, -50%);
  z-index: 1000;
  left: 50%;
  top: 80%;
  font-size: 14px;

  display: flex;
  flex-direction: column;
  font-family: 'NEXTMonoPan-Thin' !important;
  align-items: center;

  .next {
    color: white;
    background-color: #000000;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 4.6vh;
    width: 12.1vw;
    margin-bottom: 10px;
  }


  .next-disable {
    // color: white;
    background-color: #000000;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 4.6vh;
    width: 12.1vw;
    margin-bottom: 10px;

    color: gray;
    opacity: 0.8;
    // background-color: ;
  }

  .back {
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 16px;
    font-size: 12px;

  }


  .back-disable {
    color: gray;
